@font-face {
  font-family: "BebasNeueBold";
  src: url("../fonts/BebasNeueBold.woff2") format("woff2"), url("../fonts/BebasNeueBold.woff") format("woff");
  font-style: normal;
  font-weight: 700;
  font-display: swap; }

@font-face {
  font-family: "BebasNeueRegular";
  src: url("../fonts/BebasNeueRegular.woff2") format("woff2"), url("../fonts/BebasNeueRegular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

@font-face {
  font-family: "RobotoCondensed";
  src: url("../fonts/RobotoCondensedRegular.woff2") format("woff2"), url("../fonts/RobotoCondensedRegular.woff") format("woff");
  font-style: normal;
  font-weight: 400;
  font-display: swap; }

.page {
  scroll-behavior: smooth; }

.page__body-wrapper {
  overflow: hidden;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  min-width: 320px; }

.page-header {
  position: relative;
  z-index: 10; }

.page-header__container {
  display: flex;
  justify-content: space-between;
  max-width: 1160px;
  margin: 0 auto;
  padding: 0 15px; }
  @media (max-width: 1199px) {
    .page-header__container {
      padding: 0 43px; } }
  @media (max-width: 767px) {
    .page-header__container {
      padding: 0 15px; } }

.page-header__logo {
  font-size: 0; }

.logo__wrapper {
  margin-top: 30px; }
  @media (max-width: 1199px) {
    .logo__wrapper {
      margin-top: 41px; } }
  @media (max-width: 767px) {
    .logo__wrapper {
      margin-top: 21px; } }

.logo__gym {
  display: none; }

.logo__main {
  fill: #ffffff; }
  @media (max-width: 1199px) {
    .logo__main {
      width: 84px;
      height: 18px; } }
  @media (max-width: 767px) {
    .logo__main {
      width: 65px;
      height: 15px; } }

.contacts__wrapper {
  display: flex;
  flex-wrap: wrap;
  max-width: 500px;
  margin-top: 24px;
  margin-bottom: 4px; }
  @media (max-width: 1199px) {
    .contacts__wrapper {
      margin-top: 34px; } }
  @media (max-width: 767px) {
    .contacts__wrapper {
      margin-top: 14px; } }

.contacts__phone {
  margin-right: 2px;
  text-align: end; }
  @media (max-width: 1199px) {
    .contacts__phone {
      margin-right: 0; } }

.contacts__link {
  padding: 5px 0 4px;
  display: inline-block;
  vertical-align: middle;
  text-decoration: none;
  color: #ffffff;
  font-family: "BebasNeueBold", "Arial", sans-serif;
  font-weight: 700;
  font-size: 26px;
  line-height: 26px;
  letter-spacing: 0.7px; }
  @media (max-width: 1199px) {
    .contacts__link {
      font-size: 24px;
      line-height: 24px; } }
  @media (max-width: 767px) {
    .contacts__link {
      font-size: 20px;
      line-height: 20px;
      letter-spacing: 0.02em; } }

.page-main {
  flex: 1 1 auto; }

.button {
  display: inline-block;
  vertical-align: middle;
  border: 2px solid #ffffff;
  text-decoration: none;
  color: #ffffff;
  background-color: transparent;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-weight: 700;
  font-size: 12px;
  line-height: 14.06px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  padding: 9px 18px;
  cursor: pointer; }

.promo {
  background: conic-gradient(from 130.31deg at 16.03% 46.06%, #b80027 -46.69deg, #003d85 32.24deg, #b80027 313.31deg, #003d85 392.24deg);
  position: relative;
  z-index: 0;
  margin-top: -160px; }

.promo__wrapper {
  display: flex;
  justify-content: space-between;
  max-width: 1160px;
  margin: 0 auto;
  padding: 0 50px;
  padding-top: 147px;
  padding-bottom: 38px; }
  @media (max-width: 1199px) {
    .promo__wrapper {
      padding: 171px 22px 38px;
      justify-content: center;
      padding-bottom: 82px; } }
  @media (max-width: 767px) {
    .promo__wrapper {
      padding: 171px 10px 78px; } }

.promo__title-container {
  position: relative;
  padding-top: 111px; }
  @media (max-width: 1199px) {
    .promo__title-container {
      padding-top: 49px; } }
  @media (max-width: 767px) {
    .promo__title-container {
      padding-top: 51px; } }

.promo__banner-container {
  width: 500px;
  min-height: 570px;
  background: url("../../img/promo/promo-desktop.png") no-repeat;
  background-size: 100%;
  margin-right: 10.7%; }
  @media (max-width: 1199px) {
    .promo__banner-container {
      margin-right: 0;
      width: 473px;
      background: url("../../img/promo/promo-tablet.png") no-repeat;
      background-size: 93%;
      min-height: 390px; } }
  @media (max-width: 767px) {
    .promo__banner-container {
      display: none; } }

.promo__title-wrapper {
  width: 400px;
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-template-areas: "city city city" "list title title" "button button button"; }
  @media (max-width: 1199px) {
    .promo__title-wrapper {
      width: 352px;
      margin-right: 24px; } }
  @media (max-width: 767px) {
    .promo__title-wrapper {
      width: 300px;
      margin-right: 0; } }

.promo__title-wrapper h1 {
  color: #ffffff;
  margin: 0;
  font-family: "BebasNeueBold", "Arial", sans-serif;
  font-weight: 700;
  font-size: 109px;
  line-height: 109px;
  grid-area: title;
  text-align: right;
  padding: 6px 5px 0 0;
  letter-spacing: 0.03em; }
  @media (max-width: 1199px) {
    .promo__title-wrapper h1 {
      font-size: 86px;
      line-height: 86px;
      padding: 3px 5px 0 0; } }
  @media (max-width: 767px) {
    .promo__title-wrapper h1 {
      font-size: 78px;
      line-height: 78px;
      padding: 7px 5px 0 0; } }

.promo__title-wrapper span {
  text-align: right;
  color: #ffffff;
  font-family: "BebasNeueRegular", "Arial", sans-serif;
  font-weight: 400;
  font-size: 49px;
  line-height: 58.8px;
  grid-area: city;
  letter-spacing: 0.03em;
  padding-right: 5px; }
  @media (max-width: 1199px) {
    .promo__title-wrapper span {
      font-size: 34px;
      line-height: 41px; } }
  @media (max-width: 767px) {
    .promo__title-wrapper span {
      font-size: 28px;
      line-height: 34px; } }

.promo__list {
  grid-area: list;
  grid-column: 1/3;
  display: flex;
  flex-direction: column;
  width: 133px; }
  @media (max-width: 767px) {
    .promo__list {
      width: 128px; } }

.promo__button {
  grid-area: button;
  margin-left: 41.1%;
  margin-top: 57px;
  width: 53%;
  text-align: center;
  padding: 19px 6px 20px;
  border: 4px solid #ffffff; }
  @media (max-width: 1199px) {
    .promo__button {
      margin-left: 46.1%;
      margin-top: 41px;
      width: 48%; } }
  @media (max-width: 767px) {
    .promo__button {
      margin-left: 1%;
      margin-top: 44px;
      width: 91%;
      padding-bottom: 22px; } }

.promo__social-container {
  display: flex;
  align-items: end; }
  @media (max-width: 1199px) {
    .promo__social-container {
      display: none; } }

.offer-list {
  list-style: none;
  margin: 0;
  padding: 133px 0 0 9px;
  text-align: end; }
  @media (max-width: 1199px) {
    .offer-list {
      padding-top: 96px; } }
  @media (max-width: 767px) {
    .offer-list {
      padding-top: 91px;
      padding-left: 0; } }

.offer-list__item {
  color: #ffffff;
  font-family: "RobotoCondensedRegular", "Arial", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 21px;
  letter-spacing: 0.03em;
  padding-right: 10px; }

.social {
  list-style: none;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin: 0 0 45px; }

.social__item {
  padding: 8px 0; }

.social__link {
  color: #ffffff;
  text-decoration: none;
  font-family: "RobotoCondensedRegular", "Arial", sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 14.06px; }

.footer {
  background-color: #030a1a; }

